import React from "react"
import { Link } from "gatsby"
/*
        <input
          name="options[slug]"
          type="hidden"
          value={slug}
        />*/
export const Comments = ({ slug }) => {
  return (
    <div>
      <hr />
      <h2>Comments</h2>
      <h3>Add a comment</h3>
      <form method="POST" action="https://bluealloy-blog.herokuapp.com/v3/entry/github/bluealloy-bot/rakita.github.io/master/comments">
        <input name="fields[name]" type="text" placeholder="Name" required />
        <input name="fields[email]" type="email" placeholder="Email" required />
        <textarea name="fields[message]" placeholder="Comment" required />
        <button type="submit">Submit Comment</button>
      </form>
    </div>
  )
}
